import React from 'react';
import styled from 'styled-components';

const ContentStyle = styled.div`
  padding: 60px 60px 60px;
  max-width: 768px;
  margin: 0 auto;

  @media (min-width: 576px) {
    padding-top: 100px;
  }
`;

const PersonalInfoWrapper = styled.div`
  @media (min-width: 992px) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  margin-top: 75px;

  h4 {
    margin-top: 5px;
  }
`;

const IndexPage = () => (
  <ContentStyle>
    <div>
      <h1>Olá, meu nome é Jesse Mezini <br/> Desenvolvedor Front-end.</h1>

      <p>Desenvolvimento de sites reponsivos, wordpress e progressive web apps.</p>

      <PersonalInfoWrapper>
        <div>
          <h4>Informação pessoal</h4>
        </div>
        <div>
          <p>Nome: Jesse Mezini</p>
          <p>Idade: 27</p>
          <p>Enredeço: São Paulo</p>
          <p>Email: jessemezini<code>@</code>gmail.com</p>
          <p>Github: <a href="https://github.com/jessemezini">jessemezini</a></p>
        </div>
      </PersonalInfoWrapper>
    </div>
  </ContentStyle>
);

export default IndexPage;



